*{
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Helvetica, sans-serif;
}

ul, p {
  margin: 0;
  padding: 0;
}


.temp-upload {
  border: 1px dashed #BD98FF;
  border-radius: 7px;
}

.temp-center {
  display: flex;
  justify-content: center;
}

.input_box {
  position: relative;
}
.input_boxs {
  width: 100%;
  position: relative;
}


.input_box i {
  position: absolute;
  right: 10px;
  top: 10px;
}


.tempImage {
  display: none;
}
a,
a:hover {
  color: #212529;
  text-decoration: none;
}

b,
strong {
  color: #202020;
}

.btn-primary,
.btn-primary:hover {
  background: #0f3f8c;
  border-color: #0f3f8c;
  color: #fff;
}
.banner {
  background: #f8f8f8;
  padding: 140px 0;
  background-repeat: no-repeat;
  background-position: right center;
  position: relative;
}

h1 {
  font-size: 40px;
  line-height: 60px;
  font-weight: bold;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  color: #202020;
}

.mdColPadding {
  padding-left: 0.5rem;

}

@media(max-width: 768px) {
  .mdColPadding {
    padding-left: 0rem;
    padding-top: 0.5rem;
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
}
.home__container {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.home__container > * {
  margin-bottom: 10px;
}
.home__header {
  margin-bottom: 30px;
}
.username__input {
  padding: 10px;
  width: 50%;
}
.home__cta {
  width: 200px;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
  background-color: #607EAA;
  color: #F9F5EB;
  outline: none;
  border: none;
  border-radius: 5px;
}
.chat {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}
.chat__sidebar {
  height: 100%;
  background-color: #F9F5EB;
  flex: 0.2 1;
  padding: 20px;
  border-right: 1px solid #fdfdfd;
}
.chat__main {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  box-shadow: 0px 5px 10px #BD98FF27;
  border: 1px solid #a38c8c21;
  border-radius: 5px;
  padding: 1rem;
}
.chat__header {
  margin: 30px 0 20px 0;
}
.chat__users > * {
  margin-bottom: 10px;
  color: #607EAA;
  font-size: 14px;
}
.online__users > * {
  margin-bottom: 10px;
  color: rgb(238, 102, 102);
  font-style: italic;
}
.chat__mainHeader {
  width: 100%;
  height: 10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  background-color: #F9F5EB;
}
.leaveChat__btn {
  padding: 10px;
  width: 150px;
  border: none;
  outline: none;
  background-color: #D1512D;
  cursor: pointer;
  color: #EAE3D2;
}
.message__container {
  width: 100%;
  height: calc(100% - 80px);
  background-color: #fff;
  overflow-y: scroll;
}

.message__container > * {
  margin-bottom: 10px;
}
.chat__footer {
  padding: 10px;
  /* background-color: #F9F5EB; */
  height: 70px;
  width: 100%;
}
.form {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.message {
  width: 80%;
  height: 100%;
  border-radius: 10px;
  border: 1px solid #ddd;
  outline: none;
  padding: 15px;
}
.sendBtn {
  width: 150px;
  background-color: green;
  padding: 10px;
  border: none;
  outline: none;
  color: #EAE3D2;
  cursor: pointer;
}
.sendBtn:hover {
  background-color: rgb(129, 201, 129);
}
.message__recipient {
  background-color: #f5ccc2;
  width: 75%;
  padding: 10px;
  border-radius: 10px;
  font-size: 15px;
  color: #000000;
}
.message__sender{
  background-color: rgb(194, 243, 194);
  max-width: 300px;
  padding: 10px;
  border-radius: 10px;
  margin-left: auto;
  font-size: 15px;
  color: #000000;
}
.message__chats > p {
  font-size: 13px;
  color: #000000;
}
.sender__name {
  text-align: right;
}
.message__status {
  position: fixed;
  bottom: 50px;
  font-size: 13px;
  font-style: italic;
}
